.title {

    &--main {
        font-family: 'Bebas Neue', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 64px;
        line-height: 64px;
        text-transform: uppercase;
        color: #FFFFFF;
        z-index: 1;
        position: relative;
    }

    &__highlight {
        font-style: normal;
        font-weight: bold;
        font-size: 72px;
        line-height: 72px;
        text-transform: uppercase;
        color: transparent;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: #8E8E8E;
    }

    &--context {
        font-family: 'Bebas Neue', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 36px;
        line-height: 43px;
        color: #FFFFFF;
    }

    &--accordeon {
        font-family: 'SF Pro Display', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
    }
}

@media (min-width: 1220px) {
    .title {

        &--main {
            font-size: 190px;
            line-height: 190px;
        }
    }
}